import { IComponent } from '@/../models';
import * as Excel from 'exceljs';

export namespace BatchEnroll {
    export enum EFileType {
        csv = 'csv',
        xlsx = 'xlsx',
    }

    export interface IImage {
        contentType?: string;
        base64: string;
    }

    export interface IRequestToServer {
        name: string;
        image?: IImage;
        imageOrientation?: number;
        employeeId?: string;
        position?: string;
        contactNumber?: string;
        email?: string;
        card?: string;
        groupIds?: string[];
        remark?: string;
        expiredDate: string;
    }

    export interface ISelectionItem {
        group: ISelectionItemData;
    }

    export interface ISelectionItemData {
        name: string;
        datas: IComponent.ISelectionBase[];
    }

    export interface ITableTitle extends Partial<Excel.Column> {
        custom: ITableTitleCustom;
    }

    export interface ITableTitleCustom {
        isArray: boolean;
        require: boolean;
        type: EContentType;
        booleanCheck?: boolean;
        stringRegex?: RegExp;
        dateFormat?: string;
        selectionKey?: string;
        base64Length?: number;
    }

    export enum EContentType {
        none = 'none',
        boolean = 'boolean',
        int = 'int',
        number = 'number',
        string = 'string',
        date = 'date',
        image = 'image',
        phone = 'phone',
        email = 'email',
        selection = 'selection',
    }

    export interface IFileContentDataItem {
        errorMessage: string;
        original: any;
        originalText: string;
        data: any;
        dataText: string;
        dataTextArray: string[];
        selectionIdArray: string[];
    }

    export interface IExampleContent {
        no: number;
        image: string;
        employeeId: string;
        name: string;
        position: string;
        phone: string;
        email: string;
        remark: string;
        card: string;
        endDate: string;
        groupIds: string;
    }

    export interface IEnrollItemApi {
        statusCode: number | string;
        objectId: string;
        message: string;
    }

    export interface IEnrollItemFileContent {
        no?: number;
        name: IFileContentDataItem;
        groupIds: IFileContentDataItem;
        endDate: IFileContentDataItem;
        image: IFileContentDataItem;
        imageOrientation?: number;
        employeeId: IFileContentDataItem;
        position: IFileContentDataItem;
        phone: IFileContentDataItem;
        email: IFileContentDataItem;
        remark: IFileContentDataItem;
        card: IFileContentDataItem;
        api?: IEnrollItemApi;
        apiMessageKey?: string;
    }

    export interface IEnrollItem {
        useCSV: boolean;
        useExcel: boolean;
        useDirectory: boolean;
        isChooseFile: boolean;
        isChooseDirectory: boolean;
        title: ITableTitle[];
        sheetName: string;
        sampleFileName: string;
        sampleRowCount: number;
        resultFileName: string;
        file: File;
        fileError: boolean;
        fileContent: IEnrollItemFileContent[];
        fileImageArray: File[];
        csvFileType: string;
        imageStyle: string;
        maxEnrollCount: number;
        successCount: number;
        failCount: number;
        showResult: boolean;
    }
}
