





























































































































































































import { Vue, Component, Prop, Emit } from 'vue-property-decorator';

import {
    ServiceRegex,
    ServiceDatetime,
    ServiceResponseFilter,
    ServiceServerConfig,
    ServiceBase64,
    ServiceUtilities,
    ServiceDialog,
    ServiceTypeof,
} from '@/../helpers';
import { IComponent } from '@/../models';
import { IViews } from '@/models';
import { BatchEnroll as Model } from './models';

import { ServerApiPath, WebPagePath } from '@/config';

import Card from '@/../components/card/card.vue';
import WebXProgress, { EProgressMode } from '@advantech/web-x-component-progress';

import EXIF from 'exif-js';

// Excel
import * as Excel from 'exceljs';
import { saveAs } from 'file-saver';

@Component({
    components: {
        WebXProgress,
        Card,
    },
})
export default class Enroll extends Vue {
    //#region Variables & Computed
    private excelStyle: IViews.IExcelItem = {
        columnWidth: 25,
        rowHeight: 20,
        style: {
            font: {
                name: 'Microsoft JhengHei', // 字體
                color: { argb: '00000000' }, // 顏色
                size: 12,
                bold: false, // 粗體
            },
            alignment: { vertical: 'middle', horizontal: 'center', wrapText: false },
        },
        pagination: 10000,
        excelFileType: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    };

    private selectOptionItem: Model.ISelectionItem = {
        group: {
            name: '',
            datas: [],
        },
    };

    private enrollItemOriginal: Model.IEnrollItem = {
        useCSV: true,
        useExcel: true,
        useDirectory: false,
        isChooseFile: false,
        isChooseDirectory: false,
        title: [],
        sheetName: 'Enroll',
        sampleFileName: 'Batch Enroll Person',
        sampleRowCount: Math.floor(Math.random() * 10) + 10,
        resultFileName: 'Enroll Result',
        file: null,
        fileError: false,
        fileContent: [],
        fileImageArray: [],
        csvFileType: 'application/octet-stream',
        imageStyle: 'max-width: 80px;',
        maxEnrollCount: 1000,
        successCount: 0,
        failCount: 0,
        showResult: false,
    };

    private enrollItem: Model.IEnrollItem = { ...this.enrollItemOriginal };
    private get saveDisable(): boolean {
        return !this.enrollItem.isChooseFile || !this.enrollItem.isChooseDirectory || this.enrollItem.fileError;
    }

    private progressItemOriginal: IViews.IProgressItem = {
        mode: EProgressMode.count,
        value: 0,
        max: 0,
        showProgress: false,
        howManyDataATime: 3,
        progressPercentage: '0 %',
    };
    private progressItem: IViews.IProgressItem = { ...this.progressItemOriginal };

    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {
        await this.codingInitselectOptionItem();
        this.codingInitTitle();
        this.initEnrollItem();
    }
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    private async codingInitselectOptionItem(): Promise<void> {
        await this.codingInitSelectionGroup();
    }

    private async codingInitSelectionGroup(): Promise<void> {
        this.selectOptionItem.group.datas = [];
        this.selectOptionItem.group.name = 'Person Group';

        let data: any[] = JSON.parse(JSON.stringify(PERSON_GROUPS));
        data.forEach((item) =>
            this.selectOptionItem.group.datas.push({
                objectId: item.objectId,
                name: item.name,
            }),
        );
    }

    private codingInitTitle(): void {
        /**
         * header - Column title
         * key - API key
         * width - column width
         * custom.isArray - data is array, use ; break text
         * custom.require - check is require
         * custom.type - data typeof
         * custom.booleanCheck - check boolean is yes or no
         * custom.stringRegex - string need regex
         * custom.dateFormat - for Date show format
         * custom.selectionKey - for seletion check name
         * custom.base64Length - check base64 length
         */
        const width: number = this.excelStyle.columnWidth;
        const style: IViews.IExcelItemStyle = this.excelStyle.style;
        this.enrollItem.title = [
            {
                header: this.$i18n.Common_NO,
                key: 'no',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.number,
                },
            },
            {
                header: this.$i18n.Common_Photo,
                key: 'image',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false, // card or image required
                    type: Model.EContentType.image,
                    base64Length: 0,
                },
            },
            {
                header: this.$i18n.Common_EmployeeId,
                key: 'employeeId',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.string,
                },
            },
            {
                header: this.$i18n.Common_User_Name,
                key: 'name',
                width,
                style,
                custom: {
                    isArray: false,
                    require: true,
                    type: Model.EContentType.string,
                },
            },
            {
                header: this.$i18n.Common_Position,
                key: 'position',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.string,
                },
            },

            {
                header: this.$i18n.Common_Phone,
                key: 'phone',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.phone,
                },
            },
            {
                header: this.$i18n.Common_Email,
                key: 'email',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.email,
                },
            },
            {
                header: this.$i18n.Common_CardId,
                key: 'card',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false, // card or image required
                    type: Model.EContentType.string,
                },
            },
            {
                header: this.$i18n.Common_PersonGroup,
                key: 'groupIds',
                width,
                style,
                custom: {
                    isArray: true,
                    require: false,
                    type: Model.EContentType.selection,
                    selectionKey: 'group',
                },
            },
            {
                header: this.$i18n.Common_Remark,
                key: 'remark',
                width,
                style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.string,
                },
            },
            {
                header: this.$i18n.Person_Enroll_Sample_Expired_Date,
                key: 'endDate',
                width,
                style,
                custom: {
                    isArray: false,
                    require: true,
                    type: Model.EContentType.date,
                },
            },
        ];
    }

    private codingExampleContent(): Model.IExampleContent[] {
        let contents: Model.IExampleContent[] = [];
        let sampleImages = ['rd_123jn45j3k.jpg', 'qa_l23no23onf2.jpg', 'rd_lfnaewl234rtt.jpg', 'rd_dfkaljfjw23l4ol.jpg', 'rd_fk23fnjkv4k.jpg'];
        let sampleEmployeeIds = ['emp_1', 'emp_2', 'emp_3', 'emp_4', 'emp_5', 'emp_6'];
        let sampleNames = ['Jack', 'Mark', 'Bill', 'Maria', 'Joanne'];
        let samplePositions = ['RD', 'QA', 'Backend', 'Frontend', 'PG'];
        let sampleRemarks = ['', 'Docker', 'iOS', 'Android', ''];
        let sampleCards = ['11223344', '55667788', '99001122', '33445566', '77889900', '9876543210'];
        let sampleEndDate = ['2025/02/05', '2030/12/24', '2035/09/19', '2040/08/18', '2045/11/11', '2055/12/12'];
        let sampleGroup = ['VIP', 'Staff', 'Blacklist', 'Visitor', 'Admin', 'Stranger'];

        for (let i = 0; i < this.enrollItem.sampleRowCount; i++) {
            contents.push({
                no: i + 1,
                image: sampleImages[i % sampleImages.length],
                employeeId: sampleEmployeeIds[i % sampleEmployeeIds.length],
                name: sampleNames[i % sampleNames.length],
                position: samplePositions[i % samplePositions.length],
                phone: '+' + ServiceUtilities.padLeft((Math.floor(Math.random() * 999999999) + 1).toString(), '0', 10),
                email: sampleEmployeeIds[i % sampleEmployeeIds.length] + '@gmail.com',
                remark: sampleRemarks[i % sampleRemarks.length],
                card: sampleCards[i % sampleCards.length],
                endDate: sampleEndDate[i % sampleEndDate.length],
                groupIds: sampleGroup[i % sampleGroup.length],
            });
        }
        return contents;
    }

    private async codingRequestToServer(): Promise<void> {
        let param: { datas: Model.IRequestToServer[] } = { datas: [] };
        for (let content of this.enrollItem.fileContent) {
            let tempParamData: Model.IRequestToServer = {
                name: content.name.dataText,
                groupIds: content.groupIds.selectionIdArray,
                expiredDate: new Date(content.endDate.dataText).toISOString(),
            };

            if (content.image.dataText) {
                tempParamData.image = { base64: content.image.dataText };
                tempParamData.imageOrientation = content.imageOrientation;
            }

            if (content.employeeId.dataText) tempParamData.employeeId = content.employeeId.dataText;

            if (content.position.dataText) tempParamData.position = content.position.dataText;

            if (content.phone.dataText) tempParamData.contactNumber = content.phone.dataText;

            if (content.email.dataText) tempParamData.email = content.email.dataText;

            if (content.remark.dataText) tempParamData.remark = content.remark.dataText;

            if (content.card.dataText) tempParamData.card = content.card.dataText.toString();

            param.datas.push(tempParamData);
        }

        this.progressItem.max = param.datas.length;

        let response: { datas: IComponent.IStatusCodeObjectIdMessageResponse[] } = { datas: [] };

        this.progressItem.showProgress = true;

        await Batch<Model.IRequestToServer>(this.progressItem.howManyDataATime, param.datas, async (subDatas, index) => {
            await this.delay(1000);
            this.progressItem.value += this.progressItem.howManyDataATime;
            for (let i = 0; i < param.datas.length; i++) {
                response.datas.push({
                    statusCode: 200,
                    message: '',
                    objectId: '',
                });
            }
        });

        if (response.datas.length >= param.datas.length) {
            this.resolveResponse(response);
        }

        this.showProgressItemClear();
    }

    //#region Init
    private initEnrollItem(): void {
        let haveError: boolean = false;

        for (let i in this.enrollItem.title) {
            let title = this.enrollItem.title[i];

            if (!title.custom === undefined) {
                haveError = true;
                break;
            }

            if (title.custom.require === undefined) {
                haveError = true;
                break;
            }

            if (title.custom.type === undefined) {
                haveError = true;
                break;
            }

            switch (title.custom.type) {
                case Model.EContentType.boolean:
                case Model.EContentType.int:
                case Model.EContentType.number:
                case Model.EContentType.date:
                case Model.EContentType.email:
                    this.enrollItem.title[i].custom.isArray = false;
                    break;
                case Model.EContentType.image:
                    this.enrollItem.useDirectory = true;
                    this.enrollItem.title[i].custom.isArray = false;
                    break;
                case Model.EContentType.selection:
                    if (title.custom.selectionKey === undefined) {
                        haveError = true;
                    }
                    break;
            }
        }

        if (haveError) ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Status_Error, this.$i18n.Enroll_Error_Developer_Setting);
    }

    private initFileTypeCheck(): string {
        let result: string = '';
        if (this.enrollItem.useExcel && this.enrollItem.useCSV) {
            result = '.xlsx,.csv';
        }
        if (this.enrollItem.useExcel && !this.enrollItem.useCSV) {
            result = '.xlsx';
        }
        if (!this.enrollItem.useExcel && this.enrollItem.useCSV) {
            result = '.csv';
        }
        return result;
    }
    //#endregion

    private showProgressItemClear(): void {
        this.progressItem = JSON.parse(JSON.stringify({ ...this.progressItemOriginal }));
    }

    private downloadExampleCSV(): void {
        let titleText: string = '';
        let contentText: string = '';
        let fileContent: string = '';
        let contents: Model.IExampleContent[] = this.codingExampleContent();
        for (let title of this.enrollItem.title) {
            let tempTitle: string = title.header as string;
            tempTitle = tempTitle.replace(',', '');
            tempTitle = tempTitle.trim();
            titleText += `${tempTitle},`;
        }
        titleText = titleText.substr(0, titleText.length - 1);
        titleText += '\n';
        for (let content of contents) {
            for (let title of this.enrollItem.title) {
                let tempKey: string = title.key as string;
                contentText += `${content[tempKey]},`;
            }
            contentText = contentText.substr(0, contentText.length - 1);
            contentText += `\n`;
        }
        fileContent = titleText + contentText;
        fileContent = fileContent.substr(0, fileContent.length - 1);
        this.saveCSV(fileContent, this.enrollItem.sampleFileName);
    }

    private downloadExampleExcel(): void {
        let workbook: Excel.Workbook = new Excel.Workbook();
        let worksheet: Excel.Worksheet = workbook.addWorksheet(this.enrollItem.sheetName);
        let contents: Model.IExampleContent[] = this.codingExampleContent();
        worksheet.columns = this.enrollItem.title;
        for (let content of contents) {
            worksheet.addRow(content);
            worksheet.lastRow.height = this.excelStyle.rowHeight;
        }
        workbook = this.selectionSheet(workbook);
        this.saveExcel(workbook, this.enrollItem.sampleFileName);
    }

    private selectionSheet(workbook: Excel.Workbook): Excel.Workbook {
        let selection: Model.ISelectionItem = this.selectOptionItem;
        let excelWidth: number = this.excelStyle.columnWidth;
        let excelRowHeight: number = this.excelStyle.rowHeight;
        Object.keys(selection).map(function (objectKey, index) {
            let value: Model.ISelectionItemData = selection[objectKey];
            let worksheet: Excel.Worksheet = workbook.addWorksheet(value.name);
            worksheet.columns = [
                {
                    header: value.name,
                    key: 'name',
                    width: excelWidth,
                },
            ];
            for (let tempValue of value.datas) {
                worksheet.addRow({
                    name: tempValue.name,
                });
                worksheet.lastRow.height = excelRowHeight;
            }
        });
        return workbook;
    }

    private checkImageType(file: File): boolean {
        if (!file) return false;

        if (!file.name) return false;

        if (!(ServiceRegex.isExtensionJpg(file.name) || ServiceRegex.isExtensionPng(file.name))) return false;

        if (!file.type) return false;

        if (file.type.indexOf('image') >= 0) return false;

        return true;
    }

    private chooseAll(): void {
        if (this.enrollItem.isChooseFile && this.enrollItem.isChooseDirectory) {
            this.resolveImage();
        }
    }

    private async chooseDirectory(files: File[]): Promise<boolean> {
        if (!files || files.length < 1) return false;

        this.enrollItem.isChooseDirectory = true;

        this.chooseAll();
    }

    private chooseFile(file: File): boolean {
        if (!file) return false;

        let fileTypeErrorText: string = this.$i18n.Enroll_Error_File_Type;
        this.enrollItem.isChooseFile = true;
        this.enrollItem.fileContent = [];

        // 判斷檔案類型
        let extension: string = file.name.split('.')[1];

        if (!extension) {
            ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Status_Error, fileTypeErrorText);
            return false;
        }

        extension = extension.toLowerCase();

        switch (extension) {
            case Model.EFileType.xlsx:
                this.resolveExcel(file);
                break;
            case Model.EFileType.csv:
                let reader = new FileReader();
                reader.onload = (file) => {
                    if (file && file.target && file.target.result) {
                        this.resolveCSV(file.target.result as string);
                    } else {
                        ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Status_Error, fileTypeErrorText);
                        return false;
                    }
                };
                reader.readAsText(file);
                break;
            default:
                ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Status_Error, fileTypeErrorText);
                return false;
        }
    }

    //#region Resolve file content
    private resolveExcel(file: File): void {
        let workbook = new Excel.Workbook();
        let reader = new FileReader();
        reader.readAsArrayBuffer(file);
        reader.onload = () => {
            const buffer = reader.result as ArrayBuffer;
            workbook.xlsx.load(buffer).then((workbook) => {
                if (workbook.worksheets && workbook.worksheets[0]) {
                    workbook.worksheets[0].eachRow((row, rowIndex) => {
                        if (rowIndex === 1) return false;

                        let tempValues = [];
                        for (let i in row.values) {
                            let tempRowValue: any = row.values[i];
                            let tempValue: string = '';

                            // check row.values[i] type
                            if (ServiceTypeof.isString(tempRowValue) || ServiceTypeof.isNumber(tempRowValue)) {
                                tempValues[parseInt(i) - 1] = tempRowValue;
                            } else if (ServiceTypeof.isObject(tempRowValue)) {
                                if (tempRowValue && tempRowValue.richText) {
                                    // 同一欄有多種字型
                                    (tempRowValue.richText || []).forEach((element) => {
                                        if (element.text) tempValue += element.text;
                                        tempValues[parseInt(i) - 1] = tempValue;
                                    });
                                } else if (tempRowValue && tempRowValue.text && tempRowValue.hyperlink) {
                                    // 格式為email連結
                                    tempValues[parseInt(i) - 1] = tempRowValue.text;
                                } else {
                                    console.error('error => ', this.$i18n.Enroll_Error_Developer_Setting);
                                }
                            } else if (ServiceTypeof.isDate(tempRowValue)) {
                                // 格式為日期
                                tempValues[parseInt(i) - 1] = ServiceDatetime.datetime2String(tempRowValue, 'YYYY/MM/DD');
                            } else {
                                console.error('error => ', this.$i18n.Enroll_Error_Developer_Setting);
                            }
                        }
                        this.resolveFileContent(tempValues);
                        this.chooseAll();
                    });
                }
                workbook;
            });
        };
    }

    private resolveCSV(fileResult: string): void {
        let fileRows: string[] = fileResult.split('\n');
        for (let i in fileRows) {
            if (parseInt(i) === 0) continue;

            let fileRow: string = fileRows[i];
            let fileColumn: string[] = fileRow.split(',');
            this.resolveFileContent(fileColumn);
        }
        this.chooseAll();
    }

    private resolveFileContent(fileContent: string[]): void {
        let content: any = {};
        for (let index in this.enrollItem.title) {
            let title: Model.ITableTitle = this.enrollItem.title[index];
            let titleCustom: Model.ITableTitleCustom = title.custom;

            /**
             * 先塞一組資料
             * errorMessage - 錯誤訊息 & 判斷是否有錯
             * original - 原始型別的保留資料
             * originalText - 原始的文字, 如果有錯又無法顯示資料內容的, 要放原始數值 (ex: 時間)
             * data - 原始型別的資料用
             * dataText - 需要解析的, 解析過的文字內容 (ex: selection 的 name, date format 過的文字內容)
             * dataTextArray - isArray 的 dataText
             * selectionIdArray - 放 API 回傳選項的 objectId 用
             */
            let fileContentDataItem: Model.IFileContentDataItem = {
                errorMessage: '',
                original: fileContent[index],
                originalText: fileContent[index] ? fileContent[index].toString().trim() : '',
                data: fileContent[index],
                dataText: fileContent[index] ? fileContent[index].toString().trim() : '',
                dataTextArray: [],
                selectionIdArray: [],
            };
            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));

            // 檢查必傳
            if (titleCustom.require === true && !fileContentDataItem.dataText) {
                fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Require;
                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                continue;
            }

            // 設定陣列資料
            if (fileContentDataItem.dataText !== null && titleCustom.isArray) {
                let tempDataArray = fileContentDataItem.dataText.split(';');
                for (let temp of tempDataArray) {
                    temp = temp.trim();
                    if (temp) fileContentDataItem.dataTextArray.push(temp);
                }
            }

            // 型別判斷
            switch (titleCustom.type) {
                case Model.EContentType.boolean:
                    content[title.key].data = ServiceRegex.isTrueString(content[title.key].dataText) ? true : false;
                    if (titleCustom.booleanCheck !== undefined && content[title.key].data !== titleCustom.booleanCheck) {
                        fileContentDataItem.errorMessage = titleCustom.booleanCheck
                            ? this.$i18n.Enroll_Error_Boolean_True
                            : this.$i18n.Enroll_Error_Boolean_False;
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    break;

                case Model.EContentType.int:
                    let parseDataInt = parseInt(fileContentDataItem.dataText);
                    if (titleCustom.require === true) {
                        if (!parseDataInt || isNaN(parseDataInt)) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_NaN;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                    }

                    break;

                case Model.EContentType.number:
                    let parseDataFloat = parseFloat(fileContentDataItem.dataText);
                    if (titleCustom.require === true) {
                        if (!parseDataFloat || isNaN(parseDataFloat)) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_NaN;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                    }
                    break;

                case Model.EContentType.date:
                    let tempDate: Date = new Date(fileContentDataItem.dataText);
                    let tempDateString: string = ServiceDatetime.datetime2String(tempDate, 'YYYY/MM/DD');
                    fileContentDataItem.dataText = JSON.parse(JSON.stringify(tempDateString));
                    if (!titleCustom.require && !fileContentDataItem.dataText) {
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    break;

                case Model.EContentType.string:
                    if (titleCustom.isArray) {
                        if (titleCustom.require && fileContentDataItem.dataTextArray.length < 1) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Require;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        for (let tempString of fileContentDataItem.dataTextArray) {
                            if (!titleCustom.stringRegex.test(tempString)) {
                                fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Regex;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    } else {
                        if (titleCustom.stringRegex) {
                            if (!titleCustom.require && !fileContentDataItem.dataText) {
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                            if (!titleCustom.stringRegex.test(fileContentDataItem.dataText)) {
                                fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Regex;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    }
                    break;

                case Model.EContentType.phone:
                    let phoneRegex: RegExp = /(^\+{1}[0-9]+$)|(^[\d]+$)/;
                    if (titleCustom.isArray) {
                        if (titleCustom.require && fileContentDataItem.dataTextArray.length < 1) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Require;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        for (let tempString of fileContentDataItem.dataTextArray) {
                            if (!phoneRegex.test(tempString)) {
                                fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Phone;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    } else {
                        if (!titleCustom.require && !fileContentDataItem.dataText) {
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        if (!phoneRegex.test(fileContentDataItem.dataText)) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Phone;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                    }
                    break;

                case Model.EContentType.email:
                    if (typeof fileContentDataItem.data === 'object') {
                        fileContentDataItem.dataText = fileContentDataItem.data.text ? fileContentDataItem.data.text : '';
                        fileContentDataItem.originalText = JSON.stringify(fileContentDataItem.original);
                    }
                    if (!titleCustom.require && !fileContentDataItem.dataText) {
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    if (!ServiceRegex.isEmail(fileContentDataItem.dataText)) {
                        fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Email;
                        content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        continue;
                    }
                    break;

                case Model.EContentType.selection:
                    if (titleCustom.isArray) {
                        let tempSelection = this.selectOptionItem[titleCustom.selectionKey].datas;
                        if (titleCustom.require && fileContentDataItem.dataTextArray.length < 1) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Require;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        fileContentDataItem.selectionIdArray = [];

                        for (let tempString of fileContentDataItem.dataTextArray) {
                            let haveSelectionValue = false;
                            for (let selectionData of tempSelection) {
                                if (selectionData.name.toLowerCase() === tempString.toLowerCase()) {
                                    haveSelectionValue = true;
                                    fileContentDataItem.selectionIdArray.push(selectionData.objectId);
                                    break;
                                }
                            }
                            if (!haveSelectionValue) {
                                fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Data_Selection_Not_Find;
                                content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                                continue;
                            }
                        }
                    } else {
                        let tempSelection = this.selectOptionItem[titleCustom.selectionKey].datas;
                        let haveSelectionValue = false;
                        if (titleCustom.require === false && !fileContentDataItem.dataText) {
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                            continue;
                        }
                        for (let selectionData of tempSelection) {
                            if (selectionData.name.toLowerCase() === fileContentDataItem.dataText.toLowerCase()) {
                                haveSelectionValue = true;
                                fileContentDataItem.selectionIdArray = [selectionData.objectId];
                                break;
                            }
                        }
                        if (!haveSelectionValue) {
                            fileContentDataItem.errorMessage = this.$i18n.Enroll_Error_Developer_Setting;
                            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
                        }
                    }
                    break;

                case Model.EContentType.image:
                // 在 resolveImage 處理圖片
                default:
                    break;
            }

            // 最後把資料在塞一次, 避免中間有錯沒塞到
            content[title.key] = JSON.parse(JSON.stringify(fileContentDataItem));
            continue;
        }

        if (this.enrollItem.maxEnrollCount > 0) {
            if (this.enrollItem.fileContent.length < this.enrollItem.maxEnrollCount) {
                this.enrollItem.fileContent.push(content);
            }
        } else {
            this.enrollItem.fileContent.push(content);
        }
    }

    private async chooseExcelOnly(): Promise<void> {
        this.enrollItem.fileContent.forEach((item) => {
            if (!item.card.dataText && !ServiceBase64.isBase64Image(item.image.dataText)) {
                item.card.errorMessage = this.$i18n.Person_Card_Photo_Tip;
                item.image.errorMessage = this.$i18n.Enroll_Error_Image_Not_Find;
            } else if (item.card.dataText && ServiceBase64.isBase64Image(item.image.dataText)) {
                item.card.errorMessage = '';
                item.image.errorMessage = '';
            } else if (item.card.dataText) {
                item.card.errorMessage = '';
                item.image.errorMessage = '';
            } else if (ServiceBase64.isBase64Image(item.image.dataText)) {
                item.card.errorMessage = '';
                item.image.errorMessage = '';
            }
        });

        this.enrollItem.fileError = this.enrollItem.fileContent.some((item) => {
            for (let key in item) {
                if (!!item[key].errorMessage) {
                    return true;
                }
            }
        });
    }

    private async resolveImage(): Promise<void> {
        for (let titleIndex in this.enrollItem.title) {
            let title = this.enrollItem.title[titleIndex];
            let titleCustom = title.custom;
            if (titleCustom.type !== Model.EContentType.image) continue;

            for (let i in this.enrollItem.fileContent) {
                let content = this.enrollItem.fileContent[i];
                let haveImage = false;

                if (!content[title.key] || !content[title.key].dataText) {
                    continue;
                }

                for (let file of this.enrollItem.fileImageArray) {
                    if (file.name.toLowerCase() === content[title.key].dataText.toLowerCase()) {
                        haveImage = true;

                        let base64 = await ServiceBase64.getBase64(file);

                        if (base64.result) {
                            this.enrollItem.fileContent[i][title.key].dataText = base64.result;
                            this.enrollItem.title[titleIndex].custom.base64Length++;

                            let orientation: number = (await this.getExif(file)) || 1;
                            this.enrollItem.fileContent[i].imageOrientation = orientation;
                            this.checkImage();
                        } else {
                            this.enrollItem.fileContent[i][title.key].errorMessage = this.$i18n.Enroll_Error_Image_Not_Find;
                            this.enrollItem.title[titleIndex].custom.base64Length++;
                            this.checkImage();
                        }
                    }
                }
                if (!haveImage) {
                    this.enrollItem.fileContent[i][title.key].errorMessage = this.$i18n.Enroll_Error_Image_Not_Find;
                    this.enrollItem.title[titleIndex].custom.base64Length++;
                    this.checkImage();
                }
            }
        }
        this.chooseExcelOnly();
    }

    private checkImage(): void {
        for (let title of this.enrollItem.title) {
            let titleCustom = title.custom;
            if (titleCustom.type != Model.EContentType.image) {
                continue;
            }
            for (let i in this.enrollItem.fileContent) {
                let content = this.enrollItem.fileContent[i];
                if (content[title.key].dataText != '') {
                    if (ServiceBase64.isBase64Image(content[title.key].dataText)) {
                        this.enrollItem.fileContent[i][title.key].errorMessage = '';
                    } else {
                        this.enrollItem.fileContent[i][title.key].errorMessage = this.$i18n.Enroll_Error_Image_Not_Find;
                    }
                }
            }
        }
    }

    private async getExif(file): Promise<number> {
        if (file) {
            let orientation: number = await new Promise((resolve) => {
                EXIF.getData(file, () => resolve(EXIF.getTag(file, 'Orientation')));
            });
            return orientation;
        }
    }

    private resolveTableData(title, content): string {
        let result: string = '';

        if (!content[title.key]) return '';

        if (content[title.key].errorMessage) return content[title.key].originalText;

        if (title.custom.isArray) {
            result += '<ul>';
            for (let dataList of content[title.key].dataTextArray) {
                result += `<li>${dataList}</li>`;
            }
            result += '</ul>';
        } else {
            switch (title.custom.type) {
                case Model.EContentType.image:
                    if (content[title.key].dataText && content[title.key].dataText && ServiceBase64.isBase64Image(content[title.key].dataText)) {
                        result = `<img src='${content[title.key].dataText}' style='${this.enrollItem.imageStyle}'>`;
                    }
                    break;
                case Model.EContentType.date:
                case Model.EContentType.selection:
                case Model.EContentType.boolean:
                case Model.EContentType.int:
                case Model.EContentType.number:
                case Model.EContentType.string:
                case Model.EContentType.email:
                case Model.EContentType.phone:
                case Model.EContentType.none:
                default:
                    result = content[title.key].dataText;
                    break;
            }
        }

        return result;
    }

    private resolveResponse(response: { datas: IComponent.IStatusCodeObjectIdMessageResponse[] }): void {
        if (response.datas) {
            for (let i in response.datas) {
                let data = response.datas[i];
                if (this.enrollItem.fileContent[i] != undefined) {
                    this.enrollItem.fileContent[i].api = {
                        statusCode: data.statusCode ? data.statusCode : '',
                        objectId: data.objectId ? data.objectId : '',
                        message: data.message ? data.message : '',
                    };
                    if (data.statusCode == 200) {
                        this.enrollItem.successCount++;
                    } else {
                        this.enrollItem.failCount++;
                    }
                }
            }
            this.enrollItem.showResult = true;
        } else {
            ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Status_Error, this.$i18n.Error_ResponseUndefined);
        }
    }

    private resoleveAPIStatus(data: Model.IEnrollItemFileContent): boolean {
        if (data.api && data.api.statusCode) {
            return data.api && data.api.statusCode && data.api.statusCode == 200;
        } else {
            return null;
        }
    }

    private resolveAPIMessage(data: Model.IEnrollItemFileContent = undefined): string {
        if (!data.api) {
            return '';
        } else {
            if (data.api.message && !data.api.message) {
                return data.api.message;
            } else if (data.api.statusCode && data.api.statusCode === 200) {
                return this.$i18n.Enroll_Enroll_Success;
            } else {
                return `${this.$i18n.Enroll_Enroll_Failed} ${data.api.message}`;
            }
        }
    }

    private resolveFormatNames(file: File[]): string | boolean {
        if (!file) return false;
        return file.length === 1 ? file[0].name : `${file.length} ${this.$i18n.EnrollFilesSelected}`;
    }
    //#endregion

    private pageToList(): void {
        this.$emit('page-to-list');
    }

    private saveExcel(workbook: Excel.Workbook, fileName: string): void {
        workbook.xlsx.writeBuffer().then((data) => {
            let blob = new Blob([data], {
                type: this.excelStyle.excelFileType,
            });
            saveAs(blob, `${fileName}.xlsx`);
        });
    }

    private saveCSV(content: string, fileName: string): void {
        let blob = new Blob([content], {
            type: this.enrollItem.csvFileType,
        });
        saveAs(blob, `${fileName}.csv`);
    }

    private downloadResultExcel(): void {
        // 判斷是否已經存在 apiMessage 的設定
        let haveApiMessage = false;
        let apiMessageKey = 'apiMessage';
        for (let title of this.enrollItem.title) {
            if (title.key == apiMessageKey) {
                haveApiMessage = true;
                break;
            }
        }
        if (!haveApiMessage) {
            this.enrollItem.title.push({
                header: this.$i18n.Enroll_Api_Message,
                key: apiMessageKey,
                width: this.excelStyle.columnWidth,
                style: this.excelStyle.style,
                custom: {
                    isArray: false,
                    require: false,
                    type: Model.EContentType.string,
                },
            });
        }

        // Create Result Excel
        let workbook: Excel.Workbook = new Excel.Workbook();

        let i = 0;
        let successContents: Model.IEnrollItemFileContent[] = [];
        let failContents: Model.IEnrollItemFileContent[] = [];
        for (let tempData of this.enrollItem.fileContent) {
            let content: Model.IEnrollItemFileContent = {
                no: i + 1,
                image: tempData.image && tempData.image.data ? tempData.image.data : '',
                employeeId: tempData.employeeId && tempData.employeeId.data ? tempData.employeeId.data : '',
                name: tempData.name && tempData.name.data ? tempData.name.data : '',
                position: tempData.position && tempData.position.data ? tempData.position.data : '',
                phone: tempData.phone && tempData.phone.data ? tempData.phone.data : '',
                email: tempData.email && tempData.email.data ? tempData.email.data : '',
                remark: tempData.remark && tempData.remark.data ? tempData.remark.data : '',
                card: tempData.card && tempData.card.data ? tempData.card.data : '',
                endDate: tempData.endDate && tempData.endDate.data ? tempData.endDate.data : '',
                groupIds: tempData.groupIds && tempData.groupIds.data ? tempData.groupIds.data : '',
                apiMessageKey: tempData.api && tempData.api.message ? tempData.api.message : '',
            };
            content[apiMessageKey] = tempData.api && tempData.api.message ? tempData.api.message : '';
            if (tempData.api && tempData.api.statusCode && tempData.api.statusCode == 200) {
                successContents.push(content);
            } else {
                failContents.push(content);
            }
            i++;
        }

        // Fail
        let failSheet: Excel.Worksheet = workbook.addWorksheet('Fail');
        failSheet.columns = this.enrollItem.title;
        for (let content of failContents) {
            failSheet.addRow(content);
            failSheet.lastRow.height = this.excelStyle.rowHeight;
        }

        // Success
        let successSheet: Excel.Worksheet = workbook.addWorksheet('Success');
        successSheet.columns = this.enrollItem.title;
        for (let content of successContents) {
            successSheet.addRow(content);
            successSheet.lastRow.height = this.excelStyle.rowHeight;
        }

        // Save Excel
        this.saveExcel(workbook, this.enrollItem.resultFileName);
    }

    private async delay(ms: number): Promise<void> {
        await new Promise<void>((resolve) => {
            setTimeout(() => {
                return resolve();
            }, ms);
        });
    }
}

/**
 * Batch
 * @param bufferCount
 * @param datas
 * @param Do
 */
export async function Batch<T>(bufferCount: number, datas: T[], Do: (subDatas: T[]) => Promise<void>): Promise<void>;
export async function Batch<T>(bufferCount: number, datas: T[], Do: (subDatas: T[], index: number) => Promise<void>): Promise<void>;
export async function Batch<T>(bufferCount: number, datas: T[], Do: (subDatas: T[], index?: number) => Promise<void>): Promise<void> {
    try {
        for (let i: number = 0; i < datas.length; i += bufferCount) {
            await Do(datas.slice(i, i + bufferCount), i);
        }
    } catch (e) {
        throw e;
    }
}

